import React from 'react'
import Lottie from 'react-lottie';
import Animacion_Gracias from '../../Animacion/43018-thank-you-lottiefiles.json';



const defaultOptions = {
  loop:true,
  autoplay:true,
  rendererSetting: {
      preserveAspectRatio: 'xMidYMid slice'
  }
  

}; 



const Contenido_Proyecto_10 = () => {
  return (
    <div className="container-fluid animate__animated animate__zoomIn"> 

    <div className="Fondo_Proyecto_8">
      <Lottie  options={{animationData: Animacion_Gracias,defaultOptions}}/>
    </div>
    
    </div>
  )
}

export default Contenido_Proyecto_10