import React from 'react'
import ImagenHeroes from '../Imagenes/heroes.jpg';
import IconoHtml from '../Componente-2/iconhtml5.png';
import IconoCss3 from '../Componente-2/iconcss3.png';
import IconoAngular from '../Componente-2/iconangular.png';

const Contenido_Proyecto_2 = () => {
  return (
    <div className="Fondo_Proyecto_1 container-fluid animate__animated animate__zoomIn">
        

      <div className="row">

        <div className="col-md-6 Columna-PrimerContenido">
        <div className="col-md-12"><h4 ><b> Router en Angular </b></h4></div>
            <hr></hr>
          <p>Rutas en Angular y Buscador de Heroes</p>
          <div className="col-md-12">
            <p><b>Lenguajes Utilizados</b></p>
            <img className="m-1 Icono-html-Proyectos" src={IconoHtml} />
            <img className="m-1 Icono-Css3-Proyectos" src={IconoCss3} />
            <img className="m-1 Icono-Angular-Proyectos" src={IconoAngular} />
          </div>

          </div>
          <div className="col-md-6">
            <img className="ImagenSpoti" src={ImagenHeroes}/>

            <div className="col-md-12">
              <a href="https://nicolasgodoy.github.io/angular/" target="_blank" class="col-md-4 btn Botones_Estilos_2">Ver Proyecto</a>
            </div>
          </div>
           
          
          
      </div>
       


    </div>
  )
}

export default Contenido_Proyecto_2